import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function CreateEventStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default CreateEventStyle;

function setStyle(){
    eId("newEvent").style.minHeight = eId('newEvent').offsetWidth * 0.6 + 'px';

    if(window.innerWidth > window.innerHeight){
        eId('newEvent').style.borderRadius = parseInt(eId('newEvent').offsetHeight) * 0.07 + 'px';
    } else {
        eId('newEvent').style.borderRadius = parseInt(eId('newEvent').offsetWidth) * 0.07 + 'px';
    }

    if(eId('newEvent').offsetWidth > eId('newEvent').offsetHeight){
        eId("newEventForm").className = eId("newEventForm").className.replace(" mb-5", "") + " mb-5";
    } else {
        eId("newEventForm").className = eId("newEventForm").className.replace(" mb-1","") + " mb-1";
    }

    eId("newEventForm").className = eId("newEventForm").className.replace(" card-body","") + " card-body";

    setInput('input');
    setBtn('btn');
}

function setDarkLightMode(){
    darkLightMode("newEvent");
    darkLightMode('input');
    darkLightMode('button');
}

function setInput(ref){
    let input = eClass(ref);
    for(let i = 0; i < input.length; i++){
        input[i].style.borderRadius = input[0].offsetWidth * 0.025 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}